import { Button, Form, Input, message } from 'antd';
import { useEffect } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import { MAIN_PAGE, SIGNUP_PAGE } from '../../helpers/constants/constants';
import { useAppDispatch, useAppSelector } from '../../helpers/hooks/redux';
import { authSlice } from '../../store/slices/authSlice';
import { ILoginProps, postLogin } from '../../store/thunks/authThunk';
import styles from './Login.module.scss';

function Login() {
  const [form] = Form.useForm();
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const { user, isUserLogin, pendingError, isLoading } = useAppSelector(
    (state) => state.authReducer
  );
  const { handlePendingError } = authSlice.actions;

  const onFinish = (values: ILoginProps) => {
    dispatch(postLogin({ username: values.username, password: values.password }));
  };

  useEffect(() => {
    if (isUserLogin) {
      navigate(`${MAIN_PAGE}`);
    }

    return () => {
      dispatch(handlePendingError('wait'));
    };
  }, [dispatch, handlePendingError, isUserLogin, navigate, user]);

  useEffect(() => {
    if (pendingError === 'error') {
      message.error('   ');
    }
  }, [pendingError]);

  return (
    <div className={styles.login}>
      <h1>XUXO</h1>
      <Form
        form={form}
        name="horizontal_login"
        layout="vertical"
        onFinish={onFinish}
        onFinishFailed={() => message.error('   ')}
        size="large"
        className={styles.loginForm}
        labelCol={{
          xs: { span: 12, offset: 0 },
          sm: { span: 16, offset: 4 },
          md: { span: 12, offset: 6 },
          lg: { span: 8, offset: 8 },
          xl: { span: 8, offset: 8 },
          xxl: { span: 8, offset: 8 },
        }}
        wrapperCol={{
          xs: { span: 12, offset: 0 },
          sm: { span: 16, offset: 4 },
          md: { span: 12, offset: 6 },
          lg: { span: 8, offset: 8 },
          xl: { span: 8, offset: 8 },
          xxl: { span: 8, offset: 8 },
        }}
      >
        <Form.Item
          name="username"
          label=" "
          rules={[
            { required: true, message: '   !' },
            { whitespace: true },
            { min: 13, message: '     13 ' },
            { max: 13, message: '     13 ' },
          ]}
        >
          <Input style={{ width: '100%' }} />
        </Form.Item>
        <Form.Item
          name="password"
          rules={[
            { required: true, message: '   !' },
            { whitespace: true },
            { min: 8, message: '     8 ' },
          ]}
          label=""
        >
          <Input.Password />
        </Form.Item>
        <Form.Item shouldUpdate>
          <Button type="primary" htmlType="submit" style={{ width: '100%' }} loading={isLoading}>
            
          </Button>
        </Form.Item>
        <Form.Item>
          <NavLink to={`${SIGNUP_PAGE}`}></NavLink>
        </Form.Item>
      </Form>
    </div>
  );
}

export default Login;
