import { Button, Form, Input, message } from 'antd';
import { useEffect } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import { LOGIN_PAGE } from '../../helpers/constants/constants';
import { useAppDispatch, useAppSelector } from '../../helpers/hooks/redux';
import { authSlice } from '../../store/slices/authSlice';
import { IRegistrationProps, postRegistration } from '../../store/thunks/authThunk';
import styles from './SignUp.module.scss';

function SignUp() {
  const [form] = Form.useForm();
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const { pendingError, isLoading } = useAppSelector((state) => state.authReducer);
  const { handlePendingError } = authSlice.actions;

  const onFinish = async (values: IRegistrationProps) => {
    await dispatch(postRegistration(values));
  };

  useEffect(() => {
    if (pendingError === 'success') {
      navigate(`${LOGIN_PAGE}`);
    } else if (pendingError === 'error') {
      message.error(' ');
    }

    return () => {
      dispatch(handlePendingError('wait'));
    };
  }, [pendingError, navigate, dispatch, handlePendingError]);

  return (
    <div className={styles.signUp}>
      <h1>XUXO</h1>
      <Form
        form={form}
        name="horizontal_login"
        layout="vertical"
        onFinish={onFinish}
        size="large"
        labelCol={{
          xs: { span: 12, offset: 0 },
          sm: { span: 16, offset: 4 },
          md: { span: 12, offset: 6 },
          lg: { span: 8, offset: 8 },
          xl: { span: 8, offset: 8 },
          xxl: { span: 8, offset: 8 },
        }}
        wrapperCol={{
          xs: { span: 12, offset: 0 },
          sm: { span: 16, offset: 4 },
          md: { span: 12, offset: 6 },
          lg: { span: 8, offset: 8 },
          xl: { span: 8, offset: 8 },
          xxl: { span: 8, offset: 8 },
        }}
      >
        <Form.Item
          name="first_name"
          label=""
          rules={[{ required: true, message: '   !' }]}
        >
          <Input style={{ width: '100%' }} />
        </Form.Item>
        <Form.Item
          name="last_name"
          label=""
          rules={[{ required: true, message: '   !' }]}
        >
          <Input style={{ width: '100%' }} />
        </Form.Item>
        <Form.Item
          name="phone_number"
          label=" "
          rules={[
            { required: true, message: '   !' },
            { whitespace: true },
            { min: 12, message: '     12 ' },
            { max: 12, message: '     12 ' },
          ]}
        >
          <Input style={{ width: '100%' }} type="number" />
        </Form.Item>
        <Form.Item
          name="password"
          rules={[
            { required: true, message: '   !' },
            { whitespace: true },
            { min: 8, message: '     8 ' },
          ]}
          label=""
        >
          <Input.Password />
        </Form.Item>
        {/* <Form.Item
          name="address"
          label=" "
          rules={[{ required: true, message: '   !' }]}
        >
          <Input style={{ width: '100%' }} />
        </Form.Item> */}
        <Form.Item
          name="pinfl"
          label=""
          rules={[
            { required: true, message: '   !' },
            { min: 14, message: '     14 ' },
            { max: 14, message: '     14 ' },
          ]}
        >
          <Input style={{ width: '100%' }} type="number" />
        </Form.Item>
        <Form.Item
          name="passport_number"
          label=" "
          rules={[
            { required: true, message: '    !' },
            { min: 9, message: '      9 ' },
            { max: 9, message: '      9 ' },
          ]}
        >
          <Input style={{ width: '100%' }} />
        </Form.Item>

        <Form.Item shouldUpdate>
          <Button type="primary" htmlType="submit" style={{ width: '100%' }} loading={isLoading}>
            
          </Button>
        </Form.Item>
        <Form.Item>
          <NavLink to={`${LOGIN_PAGE}`}></NavLink>
        </Form.Item>
      </Form>
    </div>
  );
}

export default SignUp;
